import { mapActions } from 'vuex'

export default {
  data() {
    return {
      categories: []
    }
  },
  methods: {
    ...mapActions({
      getCategories: 'getCategories'
    })
  },
  created() {
    this.getCategories()
      .then(categories => {
        this.categories = categories;
      })
      .catch(error => {
        console.error(error)
      })
  }
}
